.modal {
   position: fixed;
   left: 0;
   top: 0;
   z-index: 100;
   overflow: auto;
   background: #101010c3;
   width: 100vw;
   height: 100vh;

   &__close {
      position: absolute;
      top: 40px;
      right: 40px;
      cursor: pointer;
      color: #fff;
   }
   &__content {
      user-select: none;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100svw;
      height: 100svh;
      margin: auto;
      > img {
         max-width: 50%;
         height: auto;
         border-radius: 25px;
         border: 1px solid var(--bd-color);

         @media (max-width: $tablet) {
            max-width: 65%;
         }
         @media (max-width: $mobile) {
            max-width: 90%;
         }
      }
   }
}
.certificates {
   padding: 20px 20px 0 20px;
   @media (max-width: $mobile) {
      padding: 10px 10px 0 10px;
   }
   &__body {
      border-radius: 30px;
      padding: 20px;
      display: flex;
      flex-direction: column;
      gap: 20px;
      border: 1px solid var(--bd-color);
      background: var(--black-p);
   }
   &__grid {
      display: grid;
      gap: 20px;
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
      @media (max-width: $tablet) {
         grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
      }
   }
}
.certificate {
   &__trigger {
      aspect-ratio: 3/1;
      background: var(--black-s);
      border-radius: 20px;
      padding: 20px 23px;
      position: relative;
      cursor: pointer;
      svg {
         position: absolute;
         bottom: 20px;
         right: 20px;
         fill: hsl(240, 0%, 10%);
      }
   }
   &__name {
      font-weight: 800;
      transition: 0.2s;
      color: hsl(240, 0%, 10%);
   }
   &__trigger:hover &__name {
      letter-spacing: 2px;
   }
}
