.item-last-works {
   padding: 10px;
   border-radius: 20px;
   padding: 20px;
   background: var(--black-s);
   border: 1px solid var(--bd-color);
   transition: background 0.3s, transform 0.1s;
   &:hover {
      background: var(--hover-b);
   }
   &:active {
      transform: scale(0.97);
   }
   &__body {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100%;
      gap: 10px;
      position: relative;
   }
   &__row {
      display: flex;
      justify-content: space-between;
      width: 100%;
      align-items: center;
   }
   &__img {
      min-height: 60px;
      min-width: 60px;
      position: relative;
      transition: 0.2s;
   }
   &__img img {
      padding: 10px;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
      border-radius: 15px;
   }
   &__img.white img {
      background: #101010;
   }
   &__img.black img {
      background: #ffffff;
   }
   &__name-site {
      font-size: 25px;
      font-weight: 700;
      padding-left: 20px;
      width: 100%;
      color: var(--white-p);
   }
   &__desctiption {
      font-style: italic;
      color: var(--white-p);
      padding: 10px;
   }
   &__data {
      color: var(--white-p);
      padding-left: 10px;
   }
   &__link:active {
      transform: scale(0.9);
   }
   svg {
      position: absolute;
      bottom: 0;
      right: 0;
      fill: var(--white-s);
   }
}
