// import fonts/font <=>
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700;800;900&display=swap');
// default font / default font size <=>
$fontFamily: 'Montserrat', sans-serif;
$fontSize: 14;
//transition for dark mode and light
* {
   transition: color 0.2s;
   transition: background 0.2s;
}
// colors palette
:root {
   --black-p: hsl(240, 0%, 10%);
   --black-s: hsl(240, 0%, 8%);
   --hover-b: hsl(240, 0%, 7%);
   --white-p: #ffffff;
   --white-s: #cccccc;
   --yellow-one: #ffff00;
   --filter: brightness(1%);
   --bd-color: #292929;
   // special colors
   --red: #ff6161;
   --yellow: #faff60;
   --green: #43d780;
}
.darkmode {
   --black-p: hsl(0, 0%, 100%);
   --black-s: hsl(0, 3%, 93%);
   --hover-b: hsl(0, 0%, 88%);
   --white-p: #111113;
   --white-s: #333333;
   --yellow-one: #0c0c0f;
   --filter: normal;
   --bd-color: #e3e3e3;
}
// max width for container <=>
$maxWidthContainer: 1250;
// size gadjes for adapted and other <=>
$tablet: 991.98px;
$mobile: 767.98px;
$mobileSmall: 479.98px;
// null style <=>
@import 'scss/_null.scss';
// general style <=>
body {
   line-height: 1.4;
   font-family: $fontFamily;
   font-size: $fontSize + px;
   background: var(--black-s);
   animation: mainanim 0.7s ease-in-out;
}
@keyframes mainanim {
   from {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}
// wrapper <=>
.wrapper {
   min-height: 100%;
   display: flex;
   flex-direction: column;
   overflow: hidden;
}
// container <=>
.container {
   max-width: $maxWidthContainer + px;
   margin: 0 auto;
   //fix for slider
   > * {
      min-width: 0;
   }
}
main {
   padding-left: 80px;
   @media (max-width: $mobile) {
      padding-left: 0;
      padding-bottom: 150px;
   }
}

// scroll bar <=>
@media only screen and (max-width: $mobile) {
   ::-webkit-scrollbar {
      display: none;
   }
}
::-webkit-scrollbar {
   background-color: var(--black-s);
   width: 16px;
}
::-webkit-scrollbar-track {
   background-color: var(--black-s);
}
::-webkit-scrollbar-thumb {
   background-color: var(--white-p);
   border-radius: 16px;
   border: 4px solid var(--black-s);
}
::-webkit-scrollbar-thumb:hover {
   background-color: var(--white-s);
}
::-webkit-scrollbar-thumb:active {
   background-color: var(--white-p);
}
::-webkit-scrollbar-button {
   display: none;
}

// when copu text<=>
::selection {
   color: #101010;
   background: var(--yellow);
}

// imports
