// title
.title {
   font-size: 30px;
   font-weight: 800;
   color: var(--white-p);
}
// see more
.seemore {
   writing-mode: vertical-lr;
   text-orientation: mixed;
   color: var(--black-p);
   background: var(--white-p);
   padding: 10px;
   text-align: center;
   border-radius: 20px;
   border: 1px solid transparent;
   transition: 0.2s;
   font-weight: 600;
}
.seemore:hover {
   color: var(--white-p);
   background: var(--black-s);
   border: 1px solid var(--bd-color);
   letter-spacing: 0.5px;
   cursor: pointer;
}

@media (max-width: $tablet) {
   .seemore {
      writing-mode: horizontal-tb;
   }
}
// lazy loading
.LazyLoad {
   opacity: 0;
   transition: opacity 1s ease-in-out;
}
.is-visible {
   opacity: 1;
}
