.intro {
   width: 100%;
   padding: 20px 20px 0 20px;
   @media (max-width: $mobile) {
      padding: 10px 10px 0 10px;
   }
   &__body {
      width: 100%;
      display: grid;
      grid-template-columns: 1.25fr 0.75fr;
      grid-template-rows: 1fr;
      border: 1px solid var(--bd-color);
      border-radius: 30px;
      padding: 20px;
      gap: 20px;
      background: var(--black-p);
   }
   &__info {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 15px;
   }
   &__title {
      font-weight: 800;
      font-size: 50px;
      line-height: 1;
      color: var(--white-p);
      @media (max-width: 1150px) {
         font-size: 30px;
      }
   }
   &__text {
      font-style: italic;
      font-size: 20px;
      flex: 1 1 auto;
      border-radius: 20px;
      padding: 20px;
      display: flex;
      justify-content: flex-start;
      align-items: flex-end;
      position: relative;
      overflow: hidden;
      z-index: 3;
      background: var(--black-s);
      color: var(--white-p);
      @media (max-width: 1300px) {
         font-size: 16px;
      }
      @media (max-width: $mobile) {
         background: var(--white-p);
         color: var(--black-p);
      }
   }
   &__achienvements {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: 1fr;
      gap: 15px;
      @media (max-width: $mobile) {
         grid-template-columns: 1fr;
         grid-template-rows: 1fr 1fr 1fr;
      }
   }
   &__item {
      border-radius: 20px;
      padding: 20px;
      flex: 1 1 auto;
      color: var(--black-s);
      color: #000;
      position: relative;
      transition: background 0.3s, transform 0.1s;
   }
   &__item svg {
      position: absolute;
      bottom: 15px;
      right: 15px;
      color: #101010;
   }
   &__item::first-line {
      font-weight: 600;
   }
   &__item:nth-child(1) {
      background: var(--yellow);
   }
   &__item:nth-child(2) {
      background: var(--red);
      &:hover {
         background: #fd7070;
      }
      &:active {
         transform: scale(0.97);
      }
   }
   &__item:nth-child(3) {
      background: var(--green);
   }
   &__img {
      height: 100%;
      width: 100%;
      position: relative;
      aspect-ratio: 1 / 1;
   }
   &__img img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
      border-radius: 20px;
   }
}
@media (max-width: $tablet) {
   .intro {
      &__body {
         display: flex;
         flex-direction: column-reverse;
         padding: 20px;
         gap: 15px;
      }
      &__info {
         padding: 00px;
      }
      &__text {
         font-size: 18px;
      }
   }
}
