.header {
   position: fixed;
   max-width: 80px;
   height: 100svh;
   width: 100%;
   z-index: 100;
   user-select: none;
   transition: max-width 0.3s;
   padding: 20px;
   @media (max-width: $mobile) {
      padding: 10px;
   }
   &__wrapper {
      height: 100%;
      max-width: 100%;
      transition: 0.3s;
      transition: max-width 0.3s;
   }
   &__body {
      background: var(--black-p);
      min-width: 60px;
      max-width: 100%;
      border-radius: 30px;
      height: 100%;
      transition: 0.3s;
      transition: max-width 0.3s;
      overflow: hidden;
      border: 1px solid var(--bd-color);
   }
   &__logo {
      position: relative;
      z-index: 5;
      background: var(--yellow-one);
      height: 40px;
      max-width: 73px;
      margin: 10px;
      border-radius: 30px;
      transition: 0.2s;
   }
   &__logo img {
      position: absolute;
      left: 0px;
      height: 40px;
      object-fit: contain;
      filter: var(--filter);
      transition: 0.2s;
      padding: 10px;
   }
   &__img-1 {
      opacity: 0;
      transition: 0.1s;
   }
   &__img-2 {
      transition: 0.1s;
   }
   &__nav {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: calc(100% - 60px);
   }
   &__burger {
      display: none;
   }
   // settings
   &__settings-btn {
      padding: 10px;
      margin: 10px;
      width: calc(100% - 20px);
      border-radius: 20px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      gap: 20px;
      color: var(--white-s);
      cursor: pointer;
      z-index: 10;
      transition: 0.15s;
      z-index: 5;
      &:hover {
         background: var(--black-s);
         color: var(--white-p);
      }
      &:active {
         transform: scale(0.9);
      }
   }
   &__settings-btn:hover .menu__icon {
      color: var(--white-p);
   }
   &__settings-icon {
      min-width: 20px;
      min-height: 20px;
      width: 20px;
      height: 20px;
   }
}

.menu {
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   flex: 1 1 auto;
   overflow: hidden;
   transition: 0.3s;

   &__body {
      height: 100%;
   }
   &__nav-text {
      visibility: hidden;
      opacity: 0;
      width: 0;
      position: relative;
      right: -15%;
      transition: 0.3s;
      white-space: nowrap;
   }
   &__list {
      max-height: 800px;
      transition: 0.3s;
      padding: 10px;
      display: flex;
      flex-direction: column;
      gap: 10px;
   }
   &__item {
      width: 100%;
      white-space: nowrap;
      height: 40px;
   }
   &__link {
      width: 100%;
      height: 40px;
      padding-left: 10px;
      display: flex;
      align-items: center;
      justify-content: left;
      gap: 20px;
      border-radius: 20px;
      cursor: pointer;
      transition: 0.1s;
      color: var(--white-s);
   }
   &__link:hover {
      background: var(--black-s);
      color: var(--white-p);
   }
   &__icon {
      color: var(--white-s);
      min-width: 20px;
      min-height: 20px;
      width: 20px;
      height: 20px;
   }
   &__link:hover &__icon {
      color: var(--white-p);
   }
}

.settings {
   height: 0;
   opacity: 0;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   align-items: flex-start;
   flex: 1 1 auto;
   overflow: hidden;
   transition: 0.3s;
   &__item {
      width: 100%;
      padding: 10px;
   }
   &__mode-btn {
      width: 100%;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: left;
      gap: 10px;
      border-radius: 20px;
      transition: 0.1s;
      color: var(--white-s);
   }
   &__mode-btn:hover {
      background: var(--black-s);
      color: var(--white-p);
   }
   &__icons {
      position: relative;
      min-width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
   }
   &__icon {
      color: var(--white-s);
      position: absolute;
      transition: 0.2s;
   }
   &__mode-btn:hover &__icon {
      color: var(--white-p);
   }
   p {
      white-space: nowrap;
   }
}
.settings__icon:nth-child(1) {
   opacity: 1;
}
.settings__icon:nth-child(2) {
   opacity: 0;
}
.darkmode .settings__icon:nth-child(1) {
   opacity: 0;
}
.darkmode .settings__icon:nth-child(2) {
   opacity: 1;
}
// animation on hover pc
.header {
   &:hover,
   &:hover &__wrapper,
   &:hover &__body {
      max-width: 220px;
      transition: max-width 0.3s;
   }
   &:hover .menu__nav-text {
      visibility: visible;
      opacity: 1;
      right: 0;
   }
   &:hover &__img-1 {
      opacity: 1;
   }
   &:hover &__img-2 {
      opacity: 0;
   }
}

.settings-open {
   .settings {
      height: auto;
      opacity: 1;
      z-index: 100;
   }
   .menu {
      &__body {
         height: 0;
      }
      &__list {
         max-height: 0;
         opacity: 0;
         pointer-events: none;
      }
   }
}

// adaptation mobile
//
@media (max-width: $mobile) {
   .header {
      position: fixed;
      max-width: 100%;
      padding: 0 0;
      min-height: 80px;
      bottom: 0;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      transition: max-height 0.3s;
      pointer-events: none;
      &__wrapper {
         min-height: 80px;
         transition: max-height 0.3s;
         height: auto;
         padding: 10px;
         pointer-events: all;
      }
      &__body {
         transition: max-height 0.3s;
         min-height: 80px;
         max-width: 100%;
         height: auto;
         position: relative;
      }
      &__logo {
         position: relative;
         height: 40px;
         max-width: none;
         width: calc(100% - 20px);
         margin: 10px;
         background: transparent;
         display: flex;
         justify-content: space-between;
      }
      &__logo::after {
         content: '';
         position: absolute;
         z-index: -1;
         background: var(--yellow-one);
         height: 40px;
         width: 73px;
         top: 0;
         left: 0;
         border-radius: 30px;
         transition: 0.2s;
      }
      &__img-1 {
         opacity: 1;
      }
      &__img-2 {
         opacity: 0;
      }
      &__nav {
         display: flex;
         transition: padding-top 0.3s;
         transition-delay: 0.15s;
      }
      &__burger {
         display: block;
         position: absolute;
         right: 0;
         width: 40px;
         height: 40px;
         border-radius: 50%;
         color: var(--black-s);
         background: var(--white-p);
      }
      &__icon-burger {
         position: relative;
         width: 100%;
         height: 100%;
         transition: 0.15s;
      }
      &__icon {
         position: absolute;
         height: 20px;
         width: 20px;
         top: 50%;
         left: 50%;
         transform: translate(-50%, -50%);
         color: var(--black-s);
      }
      &__icon:nth-child(1) {
         opacity: 1;
         transition: 0.3s;
      }
      &__icon:nth-child(2) {
         opacity: 0;
         transition: 0.3s;
      }
      // settings
      &__settings-btn {
         position: absolute;
         top: 0;
         right: 50px;
         width: auto;
         display: flex;
         justify-content: flex-end;
         align-items: center;
         border-radius: 50%;
         border: 1px solid var(--bd-color);
         background: var(--white-p);
         transition: 0.15s;
      }
      &__settings-btn:hover {
         background: var(--white-p);
      }
      &__settings-icon {
         color: var(--black-p);
         width: 20px;
         height: 20px;
      }
      &__settings:hover {
         color: var(--black-p);
         background: var(--white-p);
      }
      &__nav-text {
         display: none;
      }
   }
   //header hover
   .header {
      &:hover,
      &:hover &__wrapper,
      &:hover &__body {
         max-width: 100%;
      }
      &:hover &__img-1 {
         opacity: 1;
      }
      &:hover &__img-2 {
         opacity: 0;
      }
      &:hover .menu__nav-text {
         visibility: hidden;
         opacity: 0;
         right: 0;
      }
      &:hover .menu__nav-text {
         visibility: visible;
         opacity: 1;
      }
   }
   .menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      z-index: 10;
      max-height: 70px;
      transition: 0.3s;
      &__list {
         padding: 0 10px 10px 10px;
         width: 100%;
         display: grid;
         grid-template-columns: 1fr 1fr 1fr;
         transition: 0.3s;
      }
      &__item {
         width: 100%;
         white-space: nowrap;
         height: 60px;
      }
      &__link {
         height: 60px;
         background: var(--black-s);
         display: flex;
         flex-direction: column;
         align-items: center;
         justify-content: center;
         gap: 0;
         overflow: hidden;
         padding-left: 0;
      }
      &__icon {
         min-width: 18px;
         min-height: 18px;
      }
      &__nav-text {
         width: 100%;
         text-align: center;
         right: 0%;
         visibility: visible;
         opacity: 1;
      }
   }
   .settings {
      max-height: 70px;
      height: 70px;
      transition: 0.3s;
      opacity: 0;
      pointer-events: none;

      &:hover {
         background: transparent;
      }
      &__item {
         padding: 0 10px;
      }
   }
   // open menu
   .menu-open {
      // burger animation
      .header {
         &__icon:nth-child(1) {
            opacity: 0;
            transition: 0.3s;
         }
         &__icon:nth-child(2) {
            opacity: 1;
            transition: 0.3s;
         }
      }
      //  height of the menu openn need calculed((
      $heightOfMenu: 145px;
      .settings {
         max-height: $heightOfMenu;
         height: $heightOfMenu;
         transition: 0.3s;
         opacity: 0;
      }
      .menu {
         // calc of numbers of rows elements
         max-height: $heightOfMenu;
         transition: 0.3s;
      }
   }
   // open settings
   // numbers of elements in the
   $nrElements: 1;
   $element: 50px * $nrElements;
   //open settings
   .settings-open {
      .settings {
         max-height: $element;
         height: $element;
         opacity: 1;
         transition: 0.3s;
         pointer-events: all;
      }
      .menu {
         max-height: $element;
         transition: 0.3s;
         &__list {
            opacity: 0;
            transition: 0.3s;
         }
      }
   }
}
