.hobbyes {
   padding: 20px 20px 0 20px;
   @media (max-width: $mobile) {
      padding: 10px 10px 0 10px;
   }
   &__body {
      border: 1px solid var(--bd-color);
      padding: 20px;
      display: flex;
      flex-direction: column;
      gap: 15px;
      border-radius: 30px;
      background: var(--black-p);
   }
   &__text {
      color: var(--white-s);
      font-style: italic;
   }
   &__grid {
      display: grid;
      grid-template-rows: 1fr;
      grid-template-columns: 1fr 1fr 1fr auto;
      gap: 20px;
   }
}
.hobby {
   border-radius: 20px;
   background: var(--black-s);
   cursor: pointer;
   user-select: none;
   transition: background 0.3s, transform 0.1s;
   border: 1px solid var(--bd-color);

   &:hover {
      background: var(--hover-b);
   }
   &:active {
      transform: scale(0.97);
   }
   &__body {
      padding: 20px;
      display: flex;
      flex-direction: column;
      gap: 15px;
   }
   &__img {
      position: relative;
      width: 100%;
      aspect-ratio: 1 / 1;
   }
   &__img img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
      border-radius: 15px;
   }
   &__row {
      display: flex;
      justify-content: space-between;
      border-radius: 10px;
      font-weight: 600;
   }
   &__name {
      color: var(--white-p);
   }
   &__data {
      color: var(--white-s);
      font-style: italic;
   }
}
@media (max-width: $tablet) {
   .hobbyes {
      &__grid {
         display: grid;
         grid-template-rows: 1fr 1fr 1fr auto;
         grid-template-columns: 1fr;
         gap: 15px;
      }
   }
}
