.footer {
   padding: 20px;
   &__body {
      padding: 20px;
      background: var(--black-p);
      border-radius: 30px;
      display: flex;
      flex-direction: column;
   border: 1px solid var(--bd-color);
   gap: 20px;
   }
   &__row {
      display: flex;
      justify-content: space-between;
   }
   &__logo {
      height: 40px;
      position: relative;
      min-width: 80px;
      border-radius: 40px;
      background: var(--yellow-one);
      transition: 0.2s;
      cursor: pointer;
      z-index: 5;
      user-select: none;
      &:active {
         transform: scale(0.9);
      }
   }
   &__logo img {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      height: 20px;
      margin: 10px 0;
      object-fit: contain;
      filter: var(--filter);
      transition: 0.2s;
   }
   &__networks {
      display: flex;
      gap: 20px;
      z-index: 5;
   }
   &__network {
      height: 40px;
      width: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      background: transparent;
      position: relative;
      &::after {
         content: '';
         height: 40px;
         width: 40px;
         border-radius: 50px;
         position: absolute;
         transform-origin: center;
         z-index: -2;
         transition: 0.2s;
      }
      &::before {
         content: '';
         height: 40px;
         width: 40px;
         border-radius: 50px;
         position: absolute;
         transform-origin: center;
         z-index: -1;
         transition: 0.2s;
         background: var(--white-p);
      }
      &:hover:before {
         opacity: 0;
      }
      &:active {
         transform: scale(0.9);
      }
   }
   &__network svg {
      color: var(--black-s);
   }
   &__copy-right {
   border: 1px solid var(--bd-color);
   border-radius: 20px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      color: var(--white-s);
      background: var(--black-s);
   }
}
@media (max-width: $mobile) {
   .footer {
      padding: 10px 10px 0 10px;
      &__body {
         gap: 10px;
      }
      &__row {
         flex-direction: column;
         gap: 10px;
      }
      &__logo {
         display: none;
      }
      &__networks {
         display: grid;
         gap: 10px;
         grid-template-columns: repeat(4, 1fr);
         grid-template-rows: 1fr;
      }
      &__network {
         margin: 0;
         width: auto;
         &::after {
            width: 100%;
            min-width: 40px;
         }
         &::before {
            width: 100%;
            min-width: 40px;
         }
      }
      &__network svg {
         color: var(--black-s);
      }
   }
}
@media (max-width: $mobileSmall) {
   .footer {
      &__copy-right {
         font-size: 14px;
      }
   }
}
@media (max-width: 550px) {
   .footer {
      &__copy-right {
         font-size: 14px;
      }
   }
}
.footer__network {
   &:nth-child(1)::after {
      background: radial-gradient(
         circle at 30% 110%,
         #ffdb8b 0%,
         #ee653d 25%,
         #d42e81 50%,
         #a237b6 75%,
         #3e5fbc 100%
      );
   }
   &:nth-child(2)::after {
      background: conic-gradient(
         #4285f4,
         #4285f4,
         #db4437,
         #db4437,
         #f4b400,
         #f4b400,
         #0f9d58,
         #0f9d58,
         #4285f4
      );
   }
   &:nth-child(3)::after {
      background: #0077b5;
   }
   &:nth-child(4)::after {
      background: #4267b2;
   }
}
