.skills {
   padding: 20px 20px 0 20px;
   @media (max-width: $mobile) {
      padding: 10px 10px 0 10px;
   }
   &__body {
      border-radius: 30px;
      padding: 20px;
      display: flex;
      flex-direction: column;
      gap: 20px;
   border: 1px solid var(--bd-color);
   background: var(--black-p);
   }

   &__text {
      padding: 0 10px;
      color: var(--white-s);
   }
   &__grid {
      display: grid;
      gap: 15px;
      grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
      @media (max-width: $tablet) {
         grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      }
      @media (max-width: $mobile) {
         grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
      }
   }
}

.item-skills {
   border-radius: 20px;
   padding: 20px 23px;
   aspect-ratio: 3 / 2;
   text-align: left;
   position: relative;
   overflow: hidden;
   z-index: 2;
   cursor: pointer;
   user-select: none;
   transition: 0.2s;
   transition: background 0.3s, transform 0.1s;
   &:hover {
      background: var(--hover-b);
   }
   &:active {
      transform: scale(0.97);
   }
   &__progress {
      position: absolute;
      right: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
      z-index: -2;
      opacity: 0.6;
   }
   &__progress-bar {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      z-index: -1;
      //box-shadow: 0 0 10px rgba(0, 0, 0, 0.396);
   }

   &__text {
      z-index: 1000;
      font-weight: 800;
      transition: 0.2s;
   }
   &:hover &__text {
      letter-spacing: 2px;
   }
}
@media (max-width: $mobile) {
   .skills {
      &__body {
         padding: 20px;
      }
   }
}
